.homeContainer {
  text-align: center;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  display: flex;
}

.homeContainer img {
  filter: brightness(80%);
  border-radius: 12px;
  width: 100%;
  max-width: 600px;
  height: auto;
  margin-top: 30px;
  box-shadow: 0 4px 10px #0000001a;
}

.homeContainer h1 {
  color: #222;
  margin: 0;
  font-size: 3rem;
  font-weight: 700;
}

.homeContainer h2 {
  color: #555;
  margin: 10px 0 0;
  font-size: 1.5rem;
  font-weight: 400;
}

@media (width <= 768px) {
  .homeContainer h1 {
    font-size: 2.5rem;
  }

  .homeContainer h2 {
    font-size: 1.3rem;
  }

  .homeContainer img {
    max-width: 90%;
    margin-top: 20px;
  }
}

@media (width <= 480px) {
  .homeContainer h1 {
    font-size: 2rem;
  }

  .homeContainer h2 {
    font-size: 1.1rem;
  }

  .homeContainer img {
    max-width: 100%;
    margin-top: 15px;
  }
}
